    }
  },
  methods: {
    rowStyleHandle (row) {
      if (row.rowIndex % 2 === 0) {
        return 'background-color: #e1f9f0;height: 50px;';
      }
      return 'height: 50px;';
    }
  },computed: {
    tableMaxHeightCompu () {
      //减去header 内容区padding(上下各有两个20，共4个) card的padding
      return window.innerHeight - 60 - 20 - 20 - 40 - 100 - 100;
    }
  }
}
</script>



